html,
body,
#root {
    height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

.button {
  color: #fff;
  background-image: linear-gradient(to bottom, #444 0, #333 100%);
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  padding: 9px 15px;
  text-decoration: none;

  /* HACK styling that is navbar specific in a generic component */
  margin-top: 15px;
  margin-left: 17px;
}

.button:hover,
.button:focus {
  background-image: linear-gradient(to bottom, #555 0, #444 100%);
}

.button-link {
  color: #fff;
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  padding: 9px 15px;
  text-decoration: none;

  /* HACK styling that is navbar specific in a generic component */
  margin-top: 15px;
  margin-left: 17px;
}

.button-link:hover,
.button-link:focus {
  color: #cccccc;
}

.container {
  max-width: 1050px;
  margin: auto;
  padding: 0 14px;
  width: 100%;
}

.flex-grid {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.footer {
  background-color: #1e1e1e;
  bottom: 0;
  min-height: 215px;
  position: absolute;
  width: 100%;
}

.footer-copyright {
  color: #999;
  font-size: 12px;
  margin-top: 16px;
}

.footer-external-links {
  display: -webkit-flex;
  display: flex;
}

/* @media (max-width: 736px) {
  .footer-external-links {
    display: block;
  }
} */

.footer-link {
  color: #bdbdbd;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
}

.footer-link:not(:last-child):after {
  border-right: 1px solid #999;
  content: '';
  margin: 0 10px 0 10px;
}

.footer-link:hover,
.footer-link:focus,
.footer-link:active {
  color: #09f;
}

.footer-links {
  margin-top: 38px;
}

.footer-social-link {
  color: #fff;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-top: 42px;
  margin-right: 20px;
}

.footer-social-link:hover,
.footer-social-link:focus,
.footer-social-link:active {
  color: #0099ff;
}

.footer-social-links {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-top: 47px;
}

.footer-store-link {
  margin-left: 19px;
}

.footer-store-links {
  display: -webkit-flex;
  display: flex;
  margin-top: 42px;
  margin-left: auto;
}

.navbar {
  background-image: linear-gradient(to bottom, #09f 0, #06f 100%);
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.5);
  height: 60px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.navbar-col-left {
  -webkit-flex: 1 1;
          flex: 1 1;
}

@media (max-width: 600px) {
  .navbar-col-left {
    display: none;
  }
}

.navbar-col-right {
  margin-left: auto;
}

.navbar-heading h1 {
  color: #fff;
  font-size: 33px;
  font-weight: 600;
  margin: 0;
  padding: 12px 0;
  text-shadow: 0 1px 3px rgba(0,0,0,0.3);
}

.navbar-heading a {
  text-decoration: none;
}

.page-content {
  padding-top: 60px;
  padding-bottom: 215px;
}

.page-footer {
}

.page-header {
}

.page-wrapper {
  min-height:100%;
  position:relative;
}

.message {
}

.page-container {
  padding: 34px 0;
}

.sub-navbar {
  background-image: linear-gradient(to bottom, #444 0, #333 100%);
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.5);
  height: 56px;
  padding-top: 13px;
  width: 100%;
}

.sub-navbar-heading {
  color: #fff;
  font-size: 26px;
  font-weight: 600;
  margin: 0;
  text-shadow: 0 1px 3px rgba(0,0,0,0.3);
}

.col {
  width: 13%;
  margin-bottom: 30px;
}

@media (max-width: 980px) {
  .col {
    width: 19%;
  }
}

@media (max-width: 736px) {
  .col {
    width: 32%;
  }
}

@media (max-width: 480px) {
  .col {
    width: 49%;
  }
}

.entry-images-poster-art-url {
  width: 100%;
}

.title {
  margin-top: 5px;
  font-size: 14px;
}


